<template>
    <v-data-table hide-default-footer :headers="headers" :items="products" :search="search">
        <template v-slot:top>
            <v-toolbar flat class="pt-4">
                <v-row>
                    <v-col cols="9">
                        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line
                            hide-details>
                        </v-text-field>
                    </v-col>
                    <v-col cols="3">

                        <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn fab dark small color="indigo" v-bind="attrs" v-on="on">
                                    <v-icon dark>mdi-plus</v-icon>
                                </v-btn>
                            </template>
                            <v-card>
                                <v-toolbar dark color="primary">
                                    <v-btn icon dark @click="dialog = false">
                                        <v-icon>mdi-close</v-icon>
                                    </v-btn>
                                    <v-toolbar-title>Settings</v-toolbar-title>
                                    <v-spacer></v-spacer>
                                    <v-toolbar-items>
                                        <v-btn dark text @click="dialog = false">
                                            Save
                                        </v-btn>
                                    </v-toolbar-items>
                                </v-toolbar>
                                <v-list three-line subheader>
                                    <v-subheader>User Controls</v-subheader>
                                    <v-list-item>
                                        <v-list-item-content>
                                            <v-list-item-title>Content filtering</v-list-item-title>
                                            <v-list-item-subtitle>Set the content filtering level to restrict apps that
                                                can be downloaded</v-list-item-subtitle>
                                        </v-list-item-content>
                                    </v-list-item>
                                    <v-list-item>
                                        <v-list-item-content>
                                            <v-list-item-title>Password</v-list-item-title>
                                            <v-list-item-subtitle>Require password for purchase or use password to
                                                restrict purchase</v-list-item-subtitle>
                                        </v-list-item-content>
                                    </v-list-item>
                                </v-list>
                                <v-divider></v-divider>
                                <v-list three-line subheader>
                                    <v-subheader>General</v-subheader>
                                    <v-list-item>
                                        <v-list-item-action>
                                            <v-checkbox v-model="notifications"></v-checkbox>
                                        </v-list-item-action>
                                        <v-list-item-content>
                                            <v-list-item-title>Notifications</v-list-item-title>
                                            <v-list-item-subtitle>Notify me about updates to apps or games that I
                                                downloaded</v-list-item-subtitle>
                                        </v-list-item-content>
                                    </v-list-item>
                                    <v-list-item>
                                        <v-list-item-action>
                                            <v-checkbox v-model="sound"></v-checkbox>
                                        </v-list-item-action>
                                        <v-list-item-content>
                                            <v-list-item-title>Sound</v-list-item-title>
                                            <v-list-item-subtitle>Auto-update apps at any time. Data charges may apply
                                            </v-list-item-subtitle>
                                        </v-list-item-content>
                                    </v-list-item>
                                    <v-list-item>
                                        <v-list-item-action>
                                            <v-checkbox v-model="widgets"></v-checkbox>
                                        </v-list-item-action>
                                        <v-list-item-content>
                                            <v-list-item-title>Auto-add widgets</v-list-item-title>
                                            <v-list-item-subtitle>Automatically add home screen widgets
                                            </v-list-item-subtitle>
                                        </v-list-item-content>
                                    </v-list-item>
                                </v-list>
                            </v-card>
                        </v-dialog>
                    </v-col>
                </v-row>
            </v-toolbar>
        </template>
    </v-data-table>
</template>

<script>
import { productService } from '../services/productService';

export default {
    name: 'AdminView',
    data: () => ({
        search: '',
        dialog: false,
        notifications: false,
        sound: true,
        widgets: false,
        headers: [
            {
                text: 'Id',
                align: 'start',
                sortable: true,
                value: 'productId',
            },
            { text: 'Brand', value: 'brand' },
            { text: 'Name', value: 'name' },
            { text: 'Price', value: 'price' },
            { text: 'QuantityOnStock', value: 'quantityOnStock' },
            { text: 'Description', value: 'description' },
        ],
        products: []
    }),
    async mounted() {
        this.products = await productService.getProducts();
    },
    methods: {
    }
}
</script>

<style scoped>
</style>
